/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-die-yield-calculator {
	$color-accent: #F7B041;
	$color-good: rgb(46, 217, 46);
	$color-defective: rgb(213, 63, 49);
	$color-partial: rgba(249,249,27);
	$color-lost: rgba(151,138,129);

	color: rgb(234, 235, 236);
	font-family: Inter, sans-serif;
	font-size: 14px;

	&, * {
		box-sizing: border-box;
	}

	h2 {
		font-weight: 800;
		line-height: 1.1;
		font-size: 1.333333em;
		margin: 0 0 1em;
		color: inherit;
	}

	hr {
		height: 0;
		border: none;
		border-bottom: 1px solid #656b72;
		margin: 2em 0;
	}

	.logo {
		display: none;

		@media (min-width: 768px) {
			display: block;
			margin: 0 0 0 auto;
		}
	}

	.columns {
		display: grid;
		grid-template-columns: 1fr;
		row-gap: 4em;

		@media (min-width: 768px) {
			grid-template-columns: 45fr 55fr;
			column-gap: 2em;
		}
	}

	.panel {
		text-align: left;
		margin-bottom: 2em;

		@media (min-width: 768px) {
			border-radius: 10px;
			padding: 32px;
			background-color: #141416;
		}
	}

	label,
	legend {
		display: block;
		cursor: pointer;

		&.disabled {
			color: #B4B9BC;
		}
	}

	.output {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		text-align: center;
	}

	.results {
		display: grid;
		grid-template-columns: 1fr 1fr;
	}

	.results__list {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	.result {
		display: flex;
		align-items: center;
		margin-bottom: 0.5em;

		&::before {
			width: 1rem;
			margin-right: 0.5rem;
			line-height: 1rem;
			text-align: center;
		}

		&--good-dies,
		&--defective-dies,
		&--partial-dies,
		&--lost-dies,
		&--total-dies {
			&::before {
				content: "■";
			}
		}

		&--good-dies::before {
			color: $color-good;
		}

		&--defective-dies::before {
			color: $color-defective;
		}

		&--partial-dies::before {
			color: $color-partial;
		}

		&--lost-dies::before {
			color: $color-lost;
		}

		&--die-per-reticle::before {
			content: "▣";
		}

		&--yield::before {
			content: "◐";
			font-size: 1.125em;
		}

		&--panel-width::before {
			content: "↔";
		}

		&--panel-height::before {
			content: "↕";
		}

		&--panel-diameter::before {
			content: "⌀";
			font-size: 1.5em;
		}

		&--wafer-area::before {
			content: "◌";
		}

		&--die-area::before {
			content: "⬚";
		}

		&--shot-count::before {
			content: "☼";
		}

		&--reticle-utilization::before {
			content: "◰";
			font-size: 1.33333em;
		}

		&--waste-area::before {
			content: "⊗";
			font-size: 1.33333em;
		}
	}

	.input-row,
	.input-row--two-col {
		margin-bottom: 1rem;

		&--two-col {
			display: grid;
			grid-template-columns: 1fr 1fr;
			column-gap: 1em;
			align-items: center;
		}
	}

	input[type="text"],
	input[type="number"],
	select,
	textarea,
	input[type="checkbox"],
	input[type="radio"] {
		background-color: transparent;
		border: 1px solid #656B72;
		color: inherit;
		transition: border-color 0.2s ease-in-out;

		&:hover {
			border-color: #EAEBEC;
		}

		&:focus {
			border-color: $color-accent;
			outline: none;
		}

		&:disabled {
			cursor: not-allowed;
			border-color: #3e3e41;
			color: #B4B9BC;
		}
	}

	input[type="text"],
	input[type="number"],
	select,
	textarea {
		margin: 0;
		width: 100%;
		padding: 15px;
		border-radius: 10px;
	}

	select {
		border-radius: 14px;
		cursor: pointer;
		appearance: none;
		background-image: url("data:image/svg+xml;base64, ");
		background-position: right 15px center;
		background-repeat: no-repeat;
		background-size: 0.75em;

		// To reset Windows from displaying the wrong color
		option {
			background-color: initial;
			color: initial;
		}
	}

	input[type="checkbox"],
	input[type="radio"] {
		cursor: pointer;
		margin: 0 0.5em 0 0;
		appearance: none;
		-webkit-appearance: none;
		width: 1.5em;
		height: 1.5em;
		outline: none;
		display: inline-block;
		position: relative;

		&::before {
			content: '';
			opacity: 0;
			transition: opacity ease-out 0.1s;
		}

		&:checked::before {
			opacity: 1;
		}
	}

	input[type="checkbox"] {
		border-radius: 4px;

		&::before {
			background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgdmlld0JveD0iMCAwIDI0IDI0Ij4KICA8cGF0aCBkPSJNMjAuMjg1IDJsLTExLjI4NSAxMS41NjctNS4yODYtNS4wMTEtMy43MTQgMy43MTYgOSA4LjcyOCAxNS0xNS4yODV6IiBmaWxsPSIjRjdCMDQxIiAvPgo8L3N2Zz4=");
			background-size: 75%;
			background-position: center;
			background-repeat: no-repeat;
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
		}

	}

	input[type="radio"] {
		border-radius: 100%;

		&::before {
			width: 50%;
			height: 50%;
			background-color: #F7B041;
			border-radius: 50%;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}

	.checkbox {
		&,
		&--disabled {
			margin-bottom: 1em;

			label {
				display: flex;
				align-items: center;
			}

			input[type="checkbox"] {
				margin-right: 1em;
			}
		}

		&--disabled label {
			color: #B4B9BC;
			cursor: default;
		}
	}

	.wafer-canvas {
		display: inline-block;
		line-height: 0;
		margin-bottom: 2em;
		box-shadow: 0.25em 0.5em 1.5em black;
		transform-style: preserve-3d;
		background-color: #b88ecb;
		touch-action: none;
		max-width: 100%;

		.wafer-canvas__watermark {
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			background-image: url("../src/assets/semianalysis-logo-mark-only.png");
			background-repeat: no-repeat;
			background-position: 50% 50%;
			background-size: 60%;
			opacity: 0.075;
		}

		.wafer-canvas__mirror-background {
			border-top: 1px solid rgba(255, 255, 255, 0.5);
			border-bottom: 1px solid rgba(0, 0, 0, 0.5);
			background-image: linear-gradient(90deg,
				#b88ecb, /* Purple */
				#8eb4cd 12.5%, /* Blue */
				#9ad9cc 40%, /* Teal */
				#cc9e52 45%, /* Orange/Gold */
				#e15a4d 50%, /* Pink/Red */
				#e1b268 56%, /* Orange/Gold */
				#8fd9ca 64%, /* Teal */
				#8eb4cd 82.5%, /* Blue */
				#b88ecb 100% /* Purple */
			);
			background-size: 440%;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
		}

		&.wafer-canvas--disc {
			&,
			.wafer-canvas__mirror-background {
				// Use clip path instead of border radius to prevent tilt mouseover being triggered in corners
				clip-path: ellipse(50% 50% at 50% 50%);
				transform: rotate(45deg);
				overflow: hidden;
			}

			// Display a 1mm v-shaped notch in the bottom of the wafer
			&::after {
				content: "";
				display: block;
				width: 3px;
				height: 3px;
				background-color: #141416;
				position: absolute;
				top: 100%;
				left: 50%;
				transform: translate(-50%, -50%) rotate(45deg);
			}
		}

		.wafer-canvas__die-decorative {
			max-width: 100%;
			position: relative;
		}

		.wafer-canvas__shot-map,
		.wafer-canvas__edge,
		.wafer-canvas__die-map {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			transition: transform ease-out 0.3s, opacity ease-out 0.3s;
		}

		&:hover {
			box-shadow: 0.125em 0.25em 4em rgba(0, 0, 0, 0.75);

			.wafer-canvas__shot-map,
			.wafer-canvas__die-map,
			.wafer-canvas__edge {
				opacity: 0;
			}
		}

		.wafer-canvas__message {
			&,
			&--error {
				position: absolute;
				left: 0;
				right: 0;
				top: 50%;
				text-align: center;
				transform: translateY(-50%);
				line-height: 1;
				color: rgba(0, 0, 0, 0.5);
				text-shadow: 0 1px #ffffff2e;
			}

			&--error {
				color: $color-lost;
			}
		}
	}

	.tsmc-logo {
		position: relative;
		padding-bottom: 100%;
		margin-bottom: 2em;
		border-radius: 100%;
		overflow: hidden;

		.glare-wrapper {
			height: 95.6% !important;
		}

		svg {
			width: 100%;
			height: auto;
			position: absolute;
			top: 0;
			left: 0;
		}
	}

	fieldset {
		padding: 0;
		border: 0;
	}

	.radio-group {
		margin: 1em 0;
		display: flex;
		align-items: center;

	}

	.radio-item {
		display: flex;
		align-items: center;
		line-height: 1;
		margin: 0 1em 0 0;
	}

	.jump-to-results {
		&,
		&--hidden {
			position: fixed;
			bottom: 2em;
			left: 50%;
			padding: 10px 18px;
			font-size: 16px;
			cursor: pointer;
			background-color: $color-accent;
			color: black;
			border: none;
			border-radius: 100px;
			transform: translateX(-50%);
			transition: opacity 0.3s ease;
			border: 0;
			box-shadow: 0 0.5em 1em rgba(0, 0, 0, 0.75);

			@media (min-width: 768px) {
				display: none;
			}
		}

		&--hidden {
			opacity: 0;
			pointer-events: none;
		}
	}
}

.reticle-canvas {
	margin: auto;
	aspect-ratio: 1/1;
	max-width: calc(152.4 * 3px);
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 2em;
}

.reticle-canvas--background {
	@extend .reticle-canvas;

	background: url("../src/assets/photomaskbg.svg") no-repeat;
	background-size: contain;
	border-radius: 8px;
	overflow: hidden;
	border: 1px solid;
	border-top-color: #666c72;
	border-right-color: #46494c;
	border-bottom-color: #2d2f32;
	border-left-color: #46494c;
}

.reticle-canvas__inner {
	width: calc(104 / 152.4 * 100%);
	height: calc(132 / 152.4 * 100%);
	outline: 1px solid blue;
}
